<template>
  <div>
      <el-form-item>
        <label id="container">
          <span class="item">上传图片</span>
          <input type="file" id="btn" name="file">
        </label>
      </el-form-item>
  </div>
</template>
<script>
import qiniu from '../../util/qiniu'    //引入封装的七牛SDK方法
export default {
  data() {
      return {
          url:''
      }
  },
  methods:{
      getUrl(){
          this.$axios.qiniuGet().then(res => {
          // console.log(res)  //res.data获取token
          this.token = res.data;
          qiniu({token:res.data},(data) => {  //当上传成功后执行这个回调函数
            this.url = data.url;
            this.$emit("uploadSuccess",this.url)
            // this.formData.content += `<img src="${data.url}" />`
            //封装的七牛方法将穿过去的key封装成URL，再同过data返回，data.url就可以获取图片地址
          })
        })
      }
  },
  created() {
      this.$nextTick(() => {
          this.getUrl()
      })
  }
}
</script>
<style scoped>
#btn{
  display:none;
}
#container{
  position: absolute;
  right:250px;
  top:32px;
  z-index:5000;
}
.item{
  display:block;
  width: 80px;
  height: 30px;
  line-height:30px;
  text-align: center;
  border:1px solid #ccc;
  border-radius:4px;
  cursor: pointer;
}
.item:hover{
  color: #fff;
  background-color: #409eff;
  border-color: #409eff;
}
</style>



