<template>
  <div class="add">
    <el-form :model="formData" label-width="80px">
      <el-form-item label="标题">
        <el-input v-model="formData.title"></el-input>
      </el-form-item>
      <el-form-item label="作者">
        <el-input v-model="formData.author"></el-input>
      </el-form-item>
      <el-form-item label="上传图片">
        <el-upload
          class="avatar-uploader"
          action="https://upload-z1.qiniup.com"
          :show-file-list="false"
          :on-success="upload"
          :data="{token:token}"
          >
          <img v-if="formData.img" :src="formData.img" class="avatar">
          <i v-else class="el-icon-plus avatar-uploader-icon"></i>
        </el-upload>
      </el-form-item>
      <uploadImgBtn @uploadSuccess="getUrl"></uploadImgBtn> 
      <el-form-item>
        <editor v-model="formData.content" @getTxt="getText"></editor>
      </el-form-item>
      <el-form-item>
        <el-button @click="submit" v-if="$route.name == '新闻添加页'">提交</el-button>
        <el-button @click="edit" v-if="$route.name == '新闻修改页'">保存修改</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
  import editor from '@/components/wangEditor/editor'  //将二次封装的富文本组件引入
  import uploadImgBtn from '@/components/uploadImgBtn'
  export default {
    name: "news_add",
    components:{
      editor,
      uploadImgBtn
    },
    data(){
      return {
        formData:{
          title:'',
          author:'',
          img:'',
          content:'',
          contentText:''
        },
        token:'' //上传时附带的额外参数
      }
    },
    methods:{
      //数据回调   
      upload(res) { //res on-success钩子函数上传成功后返回的数据（key是返回的上传文件的秘钥）
        this.formData.img = "http://image.yaojunrong.com/" + res.key
      },
      getText(val){
        this.formData.contentText = val;  //获取富文本子组件传过来的纯文本值
      },
      getToken(){  //用axios方法提交  获取token
        this.$axios.qiniuGet().then(res => {
          // console.log(res)  //res.data获取token
          this.token = res.data;
        })
      },
      getUrl(url) {
        this.formData.content += `<img src="${url}" />`
      },
      submit(){
        this.$axios.post('addNews',this.formData).then((res) => {
          if(res.code == 200){
            this.$message({
              message: '恭喜你，提交成功！',
              type: 'success'
            });
            setTimeout(() => {
              this.$router.push('/news')
            }, 1000);
          }
        })
      },
      getData() {
        this.$axios.get('getNews',{id:this.$route.query.id}).then(res => {
          this.formData = res.data[0]
        })
      },
      edit() {
        this.$axios.post('updateNews',this.formData).then(res => {
          if(res.code == 200){
            this.$message({
              message: '恭喜你，修改成功！',
              type: 'success'
            });
            setTimeout(() => {
              this.$router.push('/news')
            },1000)
          }else{
            this.$message({
              message: '修改失败',
              type: 'danger'
            });
          }
        })
      }
    },
    created() {
      if(this.$route.name == '新闻修改页'){
         this.getData()
      }
      this.getToken() 
    },
    watch:{
      $route(to,from,next) {
          if(to.name == '新闻添加页'){
              this.formData = {
                  title:'',
                  author:'',
                  img:'',
                  content:'',
                  contentText:''
              }
          }
      }
  }
  }
</script>

<style>
  .avatar-uploader .el-upload {
    border: 1px dashed #d9d9d9;
    border-radius: 6px;
    cursor: pointer;
    position: relative;
    overflow: hidden;
  }
  .avatar-uploader .el-upload:hover {
    border-color: #409EFF;
  }
  .avatar-uploader-icon {
    font-size: 28px;
    color: #8c939d;
    width: 178px;
    height: 178px;
    line-height: 178px;
    text-align: center;
  }
  .avatar {
    width: 178px;
    height: 178px;
    display: block;
  }
</style>
<style scoped>
.add{
  padding-top: 20px;
  /*width: 500px;*/
}
</style>



